.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  display: flex;
}

.card__top-picture {
  width: 160px;
  height: 134px;
  margin: 32px auto;
}

.card__descript {
  color: #060b35;
  margin: 8px 16px 0;
  font-size: 12px;
  font-weight: 500;
}

.card__code {
  color: #616070;
  margin-top: 2px;
  margin-left: 16px;
  font-size: 10px;
}

.card__stars-block {
  justify-content: space-between;
  margin: 14px 16px 0;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 6px 16px 0;
  display: flex;
}

.card__price-title {
  color: #616070;
}

.card__price-amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button-buy {
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin: 0 16px 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: all .2s ease-in-out;
  display: flex;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.star:not(:first-child) {
  margin-left: 4px;
}

.stars {
  display: flex;
}

.stars :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  color: #060b35;
  font-size: 10px;
}

body {
  margin: 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 18px;
}

